@import url("https://fonts.googleapis.com/css2?family=League+Gothic&family=Source+Serif+Pro&display=swap");

$primaryFont: League Gothic, comic, sans-serif;
$secondaryFont: Source Serif pro, comic, sans-serif;

$primaryColour: #1b93c5;
$secondaryColour: radial-gradient(circle, rgba(115, 115, 115, 1) 41%, rgba(73, 71, 71, 1) 100%);
